import React,  { useState } from 'react'
import { ScrollReveal } from "reveal-on-scroll-react";
import {ReactComponent as Info} from './Assets/info.svg';
import {ReactComponent as Close} from './Assets/close.svg';
import { Fade } from 'react-awesome-reveal';

export default function Job({ icon, title, desc, info }) {

    const [infoButtonOpacity, setInfoButtonOpacity] = useState(0.0);
    const [jobInsidesDisplay, setJobInsidesDisplay] = useState('flex');
    const [closeButtonOpacity, setCloseButtonOpacity] = useState(0.0);
    const [display, setDisplay] = useState('none');

    function onHover() {
        setInfoButtonOpacity(1.0);
    }

    function onStopHover() {
        setInfoButtonOpacity(0.0);
    }

    function onOpen() {
        setDisplay('flex')
        setJobInsidesDisplay('none');
        setCloseButtonOpacity(1.0);
    }

    function onClose() {
        setDisplay('none')
        setJobInsidesDisplay('flex');
        setCloseButtonOpacity(0.0);
    }

    return (
        <div onMouseEnter={onHover} onMouseLeave={onStopHover} className="job">
            <ScrollReveal.div className="jobinsides" style={{display: jobInsidesDisplay}}>
                {icon}
                <div className="info">
                    <h1 className="subtitle">{title}</h1>
                    <h1 className="desc">{desc}</h1>
                </div>
                <Info className="infoicon" style={{opacity: infoButtonOpacity}} onClick={onOpen} />
            </ScrollReveal.div>
            <div className="jobinfo" style={{display: display}}>
                <h1 className="jobinfotext">{info}</h1>
                <Close className="closeicon" style={{opacity: closeButtonOpacity}} onClick={onClose} />
            </div>
        </div>
    )
}
