import React,  { useState } from 'react'
import { ScrollReveal } from "reveal-on-scroll-react";
import {ReactComponent as Link} from './Assets/export.svg';
import {ReactComponent as Close} from './Assets/close.svg';
import { Fade } from 'react-awesome-reveal';

export default function Job({ icon, title, desc, link }) {

    const [infoButtonOpacity, setInfoButtonOpacity] = useState(0.0);

    function onHover() {
        setInfoButtonOpacity(1.0);
    }

    function onStopHover() {
        setInfoButtonOpacity(0.0);
    }

    function openLink() {
        window.open(link, '_blank', 'noreferrer');
    }

    return (
        <div onMouseEnter={onHover} onMouseLeave={onStopHover} className="job">
            <ScrollReveal.div className="projectinsides" style={{display: 'flex'}}>
                {icon}
                <div className="info">
                    <h1 className="subtitle">{title}</h1>
                    <h1 className="desc">{desc}</h1>
                </div>
                <Link className="infoicon" style={{opacity: infoButtonOpacity}} onClick={() => openLink()} />
            </ScrollReveal.div>
        </div>
    )
}
